@import url(https://fonts.googleapis.com/css?family=Saira+Stencil+One&display=swap);
.container-fluid * {
	box-sizing: border-box;
}
.container-fluid {
	padding: 0;
	margin: 0;
}
.game_card {
	height: 100vh;
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center;
}
.game_info {
	text-align: center;
	color: #eeeeee;
	text-shadow: 1px 1px 3px black, 0 0 1em black;
	padding-top: 20%;
}
.game_name {
	font-size: 2.8em;
}
.game_dop {
	
}
.game_request {
	color: orange;
	text-transform: uppercase;
}
.game_vs {
	position: absolute;
	left: calc(50% - 65px);
	top: 25%;
	z-index: 999;
	font-family: 'Saira Stencil One', cursive;
	font-size: 6em;
	text-shadow: 1px 1px 3px black;
	color: rgba(255,76,76,1);
}
.selectbtn {
	margin-left: 5px;
	box-shadow: 1px 1px 5px black;
}
.status {
	position: absolute;
	font-size: 1.7em;
	font-family: 'Saira Stencil One', cursive;
	color: #98FF98;
	left: calc(50% - 40px);
	top: 15%;
	text-shadow: 1px 1px 3px black, 0 0 1em black;
	z-index: 999;
}
@media (max-width: 800px) {
	.game_card {
		height: calc(44vh - 3px);
	}
	.game_card:nth-child(1) {
		margin-bottom: 5px;
	}
	.status {
		top: 10%;
		font-weight: bold;
	}
	.game_vs {
		top: 10%;
	}
}
.lossescreen * {
	margin: 0; padding: 0;
}
.lossescreen {
	padding-top: 5%;
	width: 100%;
	height: 100vh;
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
}
.lossescreen h1, .lossescreen h2, .lossescreen h3 {
	text-align: center;
	color: #eeeeee;
	text-shadow: 1px 1px 5px black, 0 0 1em black;
}
.lossescreen h1 {
	font-size: 2.5em;
	padding-bottom: 54px;
}
.lossescreen h2 {
	font-size: 1.9em;
}
.lossescreen h3 {
	font-size: 1.5em;
}
.btn-play {
	padding-left: 25px;
	padding-right: 25px;
	padding-top: 5px;
	padding-bottom: 5px;
	background: rgba(0,102,255,0.8);
	border: 2px solid #3366CC;
	box-shadow: 1px 1px 4px #000033;
}
@media (max-width: 800px) {
	.lossescreen {
		height: calc(44vh - 3px);
	}
}
